@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  margin: 0;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  padding: 16px 30px;
  text-align: center;
  color: #444444;
  border: 2px solid #444444;
  background-color: #ffffff;
}

.page {
  height: 100%;
}

.page__body {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  min-height: 100%;
  margin: 0;
}

.main-header__inner {
  position: relative;
  display: flex;
  justify-content: flex-end;
  border-bottom: 1px solid #ececec;
}

.main-header__box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-30px, -20px);
}

.main-header__link-bear-logo {
  display: inline-block;
}

.main-header__wrapper--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 32px;
  right: 31px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 8px 0 0 #231f20, 0 16px 0 0 #231f20;
}

.main-header__wrapper--nojs .main-header__toggle {
  display: none;
}

.main-header__wrapper--nojs .main-header__main-nav {
  position: static;
  min-height: 0;
}

.main-header__toggle {
  position: relative;
  width: 79px;
  height: 79px;
  cursor: pointer;
  border: 0;
  background-color: transparent;
}

.main-header__wrapper--open .main-header__toggle::before, .main-header__wrapper--open .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 39px;
  right: 27px;
  width: 26px;
  height: 2px;
  background-color: #231f20;
}
.main-header__wrapper--open .main-header__toggle::before {
  transform: rotate(45deg);
}
.main-header__wrapper--open .main-header__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-header__wrapper--closed .main-header__main-nav {
    display: none;
  }
  .main-header__wrapper--nojs .main-header__main-nav {
    display: block;
  }
}
@media (min-width: 772px) {
  .main-header__logo-image {
    width: 113px;
