  margin: 0;
  row-gap: 20px;
}

.order-form__fieldset {
  border: none;
  margin: 0;
  padding: 0;
  margin-bottom: 33px;
}

.order-form__title {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 30px;
  color: #63d1bb;
  position: relative;
  text-transform: uppercase;
  width: 100%;
}
.order-form__title::after {
  top: 50%;
  left: 70px;
  position: absolute;
  content: "";
  width: calc(100% - 70px);
  height: 1px;
  background-color: #62d1ba;
}

.order-form__fields-item {
  display: flex;
  flex-direction: column;
}

.order-form__fields--type {
  row-gap: 11px;
}

.order-form__fields--gap {
  row-gap: 23px;
}

.order-form__textarea {
  font-size: 17px;
  line-height: 18px;
  font-weight: 400;
  margin: 0;
  height: 142px;
  width: 100%;
  padding: 17px;
  border: 2px solid #444444;
}
.order-form__textarea::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.order-form__textarea:focus {
  outline: none;
}

.order-form__note {
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  margin: 0;
  color: #444444;
  margin-bottom: 30px;
  display: block;
}

.order-form__fieldset--additionally {
  margin-bottom: 17px;
}

.field__input {
  font-size: 17px;
  line-height: 18px;
  font-weight: 400;
  margin: 0;
  padding: 17px 10px 17px 0;
  border: none;
  border-bottom: 2px solid #444444;
}
.field__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.field__label {
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
  margin: 0;
}

.field__label--green {
  color: #63d1bb;
  text-transform: uppercase;
  display: flex;
  white-space: nowrap;
  align-items: center;
}
.field__label--green::after {
  content: "";
  height: 1px;
  width: 100%;
  margin-left: 20px;
  background-color: #62d1ba;
}

.catalog__header {
  padding: 73px 30px;
  background-color: #f1f1f1;
}

.catalog__title {
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
}

.catalog__list {
  list-style-type: none;
  padding: 30px;
  margin: 0;
}

.catalog-video__wrapper {
  padding: 21px 30px 33px;
